<template> 
  <div>


    <button id="cargarCampo" @click="cargarCampo">
        cargar campo
    </button>

    <form id="astro1"  action="" v-on:submit.prevent="cargarInput1" method="POST" style="display: inline-block; margin-right:300px;">

        <input list="opcionesnakshatra1" type="text" v-model="dato1" name="nombre" id="imput1" placeholder="__nakshatra">
        <datalist id="opcionesnakshatra1">
            <option v-for="nakshatra in nakshatras" :key="nakshatra.id">{{nakshatra}}</option>
        </datalist>



        <button  id="cargar1" type="submit">
            cargar
        </button>
        <button  id="borrar1" @click.prevent="deleteInput1()">
            borrar
        </button>
    </form>
    
    
    <div>
        <div style="float: left;">
            <button id='limpiar' v-on:click="limpiarCampo">
                limpiar campo
            </button>
        </div>
        
    </div>
    
    
</div>
</template>



<script>



export default {
  name: 'nakshatra',
  methods:{
    
      cargarCampo(){
          this.cargarInput1();
      },

    
    
    async cargarInput1(){
        
        if(this.dato1!=""){
        let peticionTipo={nombre:'nakshatra'};
        await axios.post('/carta',peticionTipo).then((response)=>{
            let texto={
                'id':50,
                'nombre':'Nakshatra',
                'definicion': response.data
            }
            
            this.$emit('tipo1',texto);
           })
        let peticion={
            nombre:this.dato1
        }
         await axios.post('/carta',peticion).then((response)=>{
        let texto={
            'id':50,
            'nombre':this.dato1,
            'definicion': response.data
        }
        
        this.$emit('first',texto);
       })}
    
    },
    
    
    
    
    deleteInput1(){
        this.dato1="";
        let texto={'id':50}
        this.$emit('deleteDefinicion1',texto);
    }
    
    ,
    limpiarCampo(){
     this.deleteInput1();
        
      
    }
     
    
},
data(){
    return{
        dato1:'',
        
        nakshatras:[
            "ashwini",
            "bharani",
            "krittika",
            "rohini",
            "mrigashira",
            "ardra",
            "punarvasu",
            "pushya",
            "ashlesha",
            "magha",
            "purva phalguni",
            "uttara phalguni",
            "hasta",
            "chitra",
            "swati",
            "visakha",
            "anuradha",
            "Jyeshtha",
            "mula",
            "purva ashadha",
            "uttara ashadha",
            "abhijit",
            "shravana",
            "dhanishta",
            "shatabhisha",
            "purva bhadrapada",
            "uttara bhadrapada",
            "revati"
        ]
                
    }
},

created: function(){
    this.$parent.$on('clean', this.limpiarCampo);
    
    
    this.$parent.$on('update', this.cargarInput1);
}
  
}
import axios from 'axios'
</script>