<template> 
  <div>

    <button id="cargarCampo" @click="cargarCampo">
        cargar campo
    </button>

    <form id="astro1"  action="" v-on:submit.prevent="cargarInput1()" method="POST" style="display: inline-block;">

        <input list="opcionesHemisferio" type="text" v-model="dato1" name="nombre" id="imput1" placeholder="__Hemisferio vertical">
        <datalist id="opcionesHemisferio">
            <option v-for="hemisferio in hemisferioVertica" :key="hemisferio.id">{{hemisferio}}</option>        
        </datalist>



        <button  id="cargar1" type="submit">
            cargar
        </button>
        <button  id="borrar1" @click.prevent="deleteInput1()">
            borrar
        </button>
    </form>
    <form id="astro2" v-on:submit.prevent="cargarInput2()" action="" method="POST" style="display: inline-block; " >

        <input list="opcionesHemisferio" type="text" v-model="dato2" name="nombre" id="imput2" placeholder="__Hemisferio horizonal">
        <datalist id="opcionesHemisferio">
            <option v-for="hemisferio in hemisferioHorizontal" :key="hemisferio.id">{{hemisferio}}</option>       
        </datalist>






        <button  id="cargar2" type="submit">
            cargar
        </button>
        <button  id="borrar2" @click.prevent="deleteInput2()">
            borrar
        </button>
        
    </form>
    
    
        <div style="float: left; margin-top:10px;">
            <button id='limpiar' v-on:click="limpiarCampo">
                limpiar campo
            </button>
        </div>

       
    
</div>
</template>



<script>



export default {
  name: 'hemisferio',
  methods:{
    


    cargarCampo(){
        this.cargarInput1();
        this.cargarInput2();
       
    }
    ,
    async cargarInput1(){
        
        if(this.dato1!=""){
        let peticionTipo={nombre:'hemisferioVertica'};
        await axios.post('/carta',peticionTipo).then((response)=>{
            let texto={
                'id':48,
                'nombre':'hemisferio',
                'definicion': response.data
            }
            
            this.$emit('tipo1',texto);
           })
        let peticion={
            nombre:this.dato1
        }
         await axios.post('/carta',peticion).then((response)=>{
        let texto={
            'id':48,
            'nombre':this.dato1,
            'definicion': response.data
        }
        
        this.$emit('first',texto);
       })}
    
    },
    async cargarInput2(){
        
        if(this.dato2!=""){
        let peticionTipo={nombre:'hemisferioHorizontal'};
        await axios.post('/carta',peticionTipo).then((response)=>{
            let texto={
                'id':48,
                'nombre':'hemisferio',
                'definicion': response.data
            }
            
            this.$emit('tipo2',texto);
           })
        let peticion={
            nombre:this.dato2
        }
         await axios.post('/carta',peticion).then((response)=>{
        let texto={
            'id':48,
            'nombre':this.dato2,
            'definicion': response.data
        }
        
        this.$emit('second',texto);
       })}
    
    },
    
    
    
    
    deleteInput1(){
        this.dato1="";
        let texto={'id':48}
        this.$emit('deleteDefinicion1',texto);
    },
     deleteInput2(){
        this.dato2="";
        let texto={'id':48}
        this.$emit('deleteDefinicion2',texto);
    }
    
    ,
    limpiarCampo(){
     this.deleteInput1();
     this.deleteInput2();        
      
    }
     
    
},
data(){
    return{
        dato1:'',
        dato2:'',
        
        hemisferioHorizontal:[
            "hemisferio occidental",
            "hemisferio oriental",
            
            
        ],
        hemisferioVertica:[
            "hemisferio sur",
            "hemisferio norte"
        ]
                
    }
},

created: function(){
    this.$parent.$on('clean', this.limpiarCampo);
    
    
    this.$parent.$on('update', this.cargarCampo);
}
  
}
import axios from 'axios'
</script>