<template> 
 <div>


    <button id="cargarCampo" @click="cargarCampo">
        cargar campo
    </button>

    <form id="astro1"  action="" v-on:submit.prevent="cargarInput1()" method="POST" style="display: inline-block;">

        <input list="opcionesPluton1" type="text" v-model="dato1" name="nombre" id="imput1" placeholder="__pluton en signo">
        <datalist id="opcionesPluton1">
          <option v-for="signo in signosPluton" :key="signo.id">{{signo}}</option> 
        </datalist>



        <button  id="cargar1" type="submit">
            cargar
        </button>
        <button  id="borrar1" @click.prevent="deleteInput1()">
            borrar
        </button>
    </form>
    <form id="astro2" v-on:submit.prevent="cargarInput2()" action="" method="POST" style="display: inline-block; " >

        <input list="opcionesPluton2" type="text" v-model="dato2" name="nombre" id="imput2" placeholder="__pluton en casa">
        <datalist id="opcionesPluton2">
            <option v-for="casa in casasPluton" :key="casa.id">{{casa}}</option>
        </datalist>






        <button  id="cargar2" type="submit">
            cargar
        </button>
        <button  id="borrar2" @click.prevent="deleteInput2()">
            borrar
        </button>
        
    </form>
    
    <div >
        <div style="float: left;">
            <button id='limpiar' v-on:click="limpiarCampo">
                limpiar campo
            </button>
        </div>
        <form id="aspectos"  v-on:submit.prevent="cargarInput3()" action="" style="position:relative; left: 200px; margin-top: 10px;" >
        
        <div id="imput3"  style="float:left">
            <div v-for="(find,index) in dato3" :key="find.id">
                <input list="opcionesPluton" v-model="find.value" :key="index" placeholder="__aspectos del pluton">
                <datalist id="opcionesPluton">
                    <option v-for="aspecto in aspectos" :key="aspecto.id">{{aspecto}}</option>
                </datalist>



            </div>
        </div>
        
        <div style="position: absolute; left:280px">
            <button  id="agregar3" @click.prevent= "addAspecto">
                agregar
            </button>
            <button  id="borrar3" @click.prevent="deleteAspecto">
                borrar
            </button>
            <div>
                <button  id="enviaraspectos" type="submit">
                    cargar aspectos
                </button>
                <div style="clear: both;">

                </div>
            </div>
            
        </div>
        
        </form>
    </div>
    
    
</div>
</template>



<script>



export default {
  name: 'pluton',
  methods:{
    


    cargarCampo(){
        this.cargarInput1();
        this.cargarInput2();
        this.cargarInput3();
    }
    ,
    async cargarInput1(){
        
        if(this.dato1!=""){
        let peticionTipo={nombre:'pluton_ensigno'};
        await axios.post('/carta',peticionTipo).then((response)=>{
            let texto={
                'id':24,
                'nombre':'pluton en el signo',
                'definicion': response.data
            }
            
            this.$emit('tipo1',texto);
           })
        let peticion={
            nombre:this.dato1
        }
         await axios.post('/carta',peticion).then((response)=>{
        let texto={
            'id':24,
            'nombre':this.dato1,
            'definicion': response.data
        }
        
        this.$emit('first',texto);
       })}
    
    },
    async cargarInput2(){
        if(this.dato2!=""){
        let peticionTipo={nombre:'pluton_encasa'};
        await axios.post('/carta',peticionTipo).then((response)=>{
            let texto={
                'id':24,
                'nombre':'pluton en la Casa',
                'definicion': response.data
            }
            
            this.$emit('tipo2',texto);
           })
        let peticion={
            nombre:this.dato2
        }
         await axios.post('/carta',peticion).then((response)=>{
        let texto={
            'id':24,
            'nombre':this.dato2,
            'definicion': response.data
        }
        
        this.$emit('second',texto);
       })}
    
    },
    async cargarInput3(){

         let {value}=this.dato3[0];
        
       if (value!=''){   
       
        await axios.post('/aspectos',this.dato3).then((response)=>{        
        let nombre=response.data.value;
        let definicion=response.data.respuestas;
        let data=new Array(definicion.length);
        let i=0;
        nombre.forEach(element => {
           if(definicion[i]!=""){
            data[i]={
                'nombre':element,
                'definicion':definicion[i]
            }}else{data[i]={'nombre':'','definicion':''}
        }
            i++;
        });
        let texto={
            'id':24,
            'aspectos':data
        }
        this.$emit('third',texto);
        
      });
    }
    }
    
    ,
    addAspecto:function(){
        this.dato3.push({value:''})
        
    }
    ,
    deleteAspecto:function(index){
        console.log(index);
        console.log(this.dato3);
        let ind=this.dato3.length;
        this.dato3.splice(ind-1,1);
        let texto={'id':24}
        this.$emit('deleteAspecto',texto);
    }
    ,
    deleteInput1(){
        this.dato1="";
        let texto={'id':24}
        this.$emit('deleteDefinicion1',texto);
    }
    ,
    deleteInput2(){
        this.dato2="";
        let texto={'id':24}
        this.$emit('deleteDefinicion2',texto);
    },
    deleteAspectos(){
        this.dato3=[];
        let texto={'id':24}
        this.$emit('deleteAspectos',texto)
    }
    ,
    limpiarCampo(){
     this.deleteInput1();
     this.deleteInput2();   
     this.deleteAspectos();
     this.addAspecto();   
    }
     
    
},

data(){
    return{
        dato1:'',
        dato2:'',
        count:0,
        dato3:[],
        casasPluton:[
          "plutón en casa 1",
            "plutón en casa 2",
            "plutón en casa 3",
            "plutón en casa 4",
            "plutón en casa 5",
            "plutón en casa 6",
            "plutón en casa 7",
            "plutón en casa 8",
            "plutón en casa 9",
            "plutón en casa 10",
            "plutón en casa 11",
            "plutón en casa 12"
        ],
      signosPluton:[
         "plutón en aries",
            "plutón en tauro",
            "plutón en geminis",
            "plutón en cancer",
            "plutón en leo",
            "plutón en virgo",
            "plutón en libra",
            "plutón en escorpio",
            "plutón en sagitario",
            "plutón en capricornio",
            "plutón en acuario",
            "plutón en piscis"
      ],
       aspectos:[
         "plutón conjunción ",
                    "plutón trígono ",
                    "plutón sextil ",
                    "plutón cuadratura ",
                    "plutón oposición "
       ]         
    }
}
,
mounted(){
    this.addAspecto()
},


  created: function(){
    this.$parent.$on('clean', this.limpiarCampo);
    
    
    this.$parent.$on('update', this.cargarCampo);
  }
}

import axios from 'axios'
  

</script>