<template> 

  <div>

  <button id="cargarCampo" @click="cargarCampo">
        cargar campo
    </button>

    <form id="astro1"  action="" v-on:submit.prevent="cargarInput1()" method="POST" style="display: inline-block;">

        <input list="opcionesAscendente" type="text" v-model="dato1" name="nombre" id="imput1" placeholder="__ascendente">
        <datalist id="opcionesAscendente">
            <option v-for="ascendente in ascendentes" :key="ascendente.id">{{ascendente}}</option>        
        </datalist>



        <button  id="cargar1" type="submit">
            cargar
        </button>
        <button  id="borrar1" @click.prevent="deleteInput1()">
            borrar
        </button>
    </form>
    <form id="astro2" v-on:submit.prevent="cargarInput2()" action="" method="POST" style="display: inline-block; " >

        <input list="opcionesMedioCielo" type="text" v-model="dato2" name="nombre" id="imput2" placeholder="__medio Cielo">
        <datalist id="opcionesMedioCielo">
            <option v-for="medioCielo in medioCielos" :key="medioCielo.id">{{medioCielo}}</option>       
        </datalist>






        <button  id="cargar2" type="submit">
            cargar
        </button>
        <button  id="borrar2" @click.prevent="deleteInput2()">
            borrar
        </button>
        
    </form>
    
    
        <div style="float: left; margin-top:10px;">
            <button id='limpiar' v-on:click="limpiarCampo">
                limpiar campo
            </button>
        </div>
        
    </div>

</template>



<script>



export default {
  name: 'ascendenteMedioCielo',
  data(){
    return{
      dato1:'',
        dato2:'',
      ascendentes:[
            "ascendente en aries",
            "ascendente en tauro",
            "ascendente en geminis",
            "ascendente en cancer",
            "ascendente en leo",
            "ascendente en virgo",
            "ascendente en libra",
            "ascendente en escorpio",
            "ascendente en sagitario",
            "ascendente en capricornio",
            "ascendente en acuario",
            "ascendente en piscis"
            ],
      medioCielos:[
            "medio cielo en aries",
            "medio cielo en tauro",
            "medio cielo en geminis",
            "medio cielo en cancer",
            "medio cielo en leo",
            "medio cielo en virgo",
            "medio cielo en libra",
            "medio cielo en escorpio",
            "medio cielo en sagitario",
            "medio cielo en capricornio",
            "medio cielo en acuario",
            "medio cielo en piscis"
      ]


    }

 },
  methods:{
    


    cargarCampo(){
        this.cargarInput1();
        this.cargarInput2();
        
    }
    ,
    async cargarInput1(){
        
        if(this.dato1!=""){
        let peticionTipo={nombre:'ascendente'};
        await axios.post('/carta',peticionTipo).then((response)=>{
            let texto={
                'id':2,
                'nombre':'Ascendente en el signo',
                'definicion': response.data
            }
            
            this.$emit('tipo1',texto);
           })
        let peticion={
            nombre:this.dato1
        }
         await axios.post('/carta',peticion).then((response)=>{
        let texto={
            'id':2,
            'nombre':this.dato1,
            'definicion': response.data
        }
        
        this.$emit('first',texto);
       })}
    
    },
    async cargarInput2(){
        if(this.dato2!=""){
        let peticionTipo={nombre:'medio_cielo'};
        await axios.post('/carta',peticionTipo).then((response)=>{
            let texto={
                'id':4,
                'nombre':'Medio Cielo en el signo',
                'definicion': response.data
            }
            
            this.$emit('tipo1',texto);
           })
        let peticion={
            nombre:this.dato2
        }
         await axios.post('/carta',peticion).then((response)=>{
        let texto={
            'id':4,
            'nombre':this.dato2,
            'definicion': response.data
        }
        
        this.$emit('first',texto);
       })}
    
    }
    ,
    deleteInput1(){
        this.dato1="";
        let texto={'id':2}
        this.$emit('deleteDefinicion1',texto);
    }
    ,
    deleteInput2(){
        this.dato2="";
        let texto={'id':4}
        this.$emit('deleteDefinicion1',texto);
    }
    ,
    limpiarCampo(){
     this.deleteInput1();
     this.deleteInput2();   
       
    }
     
    
},
created: function(){
    this.$parent.$on('clean', this.limpiarCampo);
    
    
    this.$parent.$on('update', this.cargarCampo);
}
}

import axios from 'axios'
</script>